ks-section {
    display: flex;

    justify-content: center;
    align-items: center;
    min-height: calc(100dvh - 595px);

    box-sizing: border-box;
    padding: 30px 0px;
}

ks-section[muted] {
    background: #f4f4f4;
}

ks-section .container {
    box-sizing: content-box;
    max-width: 1200px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

@media (max-width: 1260px) {
    ks-section {
        padding: 0px !important;
    }

    ks-section .container {
        max-width: 100%;
    }
}